import React, { useState } from 'react';

const Map = React.lazy(() => import("../../../../components/map/Map"));

const MapSection = () => {

    const [btn1bg, setBtn1Bg] = useState("#f3ca3e");
    const [btn2bg, setBtn2Bg] = useState("#d5dbe0");
    const [disableinput, setDisableInput] = useState();

    const Handlebtn1Click = () => {
        setBtn2Bg("#d5dbe0");
        setBtn1Bg("#f3ca3e");
        setDisableInput(false);
    }

    const Handlebtn2Click = () => {
        setBtn1Bg("#d5dbe0");
        setBtn2Bg("#f3ca3e");

        setDisableInput(true);
    }

    return (
        <>
            <div className="col-md-4 pe-md-5 pe-2 ">
                <div className="card border-0">
                    <div className="card-body m-md-4">
                        <h6 className='text-secondary'>PRODUCT TYPE:</h6>

                        <div className="btn-group rounded" role="group" aria-label="Basic example">
                            <button type="button" onClick={Handlebtn1Click} className="btn" style={{ background: btn1bg }}><i className="fa-solid fa-house-tsunami me-2"></i> Outdoor Blinds</button>
                            <button type="button" onClick={Handlebtn2Click} className="btn" style={{ background: btn2bg }}><i className="fa-solid fa-house me-2"></i> Interior Blinds</button>
                        </div>

                        <div className='mt-4 small'>
                            <p className='text-secondary'>STORE:</p>

                            <div className="mt-3">
                                <div className="d-flex justify-content-between">
                                    <div className='d-flex text-secondary'>
                                        <span className='me-2'><i className="fa-solid fa-hotel"></i></span>
                                        <p className='card-text'>Showroom</p>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input p-2 border-2 border-secondary" type="checkbox" id="flexCheckDefault" />
                                    </div>
                                </div>
                            </div>

                            <div className="mt-3">
                                <div className="d-flex justify-content-between">
                                    <div className='d-flex text-secondary'>
                                        <span className='me-2'><i className="fa-solid fa-truck-fast"></i></span>
                                        <p className='card-text'>Mobile Consultant</p>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input p-2 border-2 border-secondary" type="checkbox" id="flexCheckDefault" />
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div className='mt-4 small'>
                            <p className='text-secondary'>SERVICE:</p>

                            <div className="mt-3">
                                <div className="d-flex justify-content-between">
                                    <div className='d-flex text-secondary'>
                                        <span className='me-2'><i className="fa-solid fa-screwdriver-wrench"></i></span>
                                        <p className='card-text'>Services and Reapairs</p>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input p-2 border-2 border-secondary" type="checkbox" id="flexCheckDefault" />
                                    </div>
                                </div>
                            </div>

                            <div className="mt-3">
                                <div className="d-flex justify-content-between">
                                    <div className='d-flex text-secondary'>
                                        <span className='me-2'><i className="fa-solid fa-shuffle"></i></span>
                                        <p className="card-text">Swiftee Stockist</p>
                                    </div>
                                    <div className="form-check">
                                        <input disabled={disableinput} className="form-check-input p-2 border-2 border-secondary" type="checkbox" id="flexCheckDefault" />
                                    </div>
                                </div>
                            </div>

                        </div>

                    </div>
                </div>
            </div>
            <div className="col-md-8">
                <Map />
            </div>

        </>
    )
}

export default MapSection